import React from 'react';
import './CSS/main.css';
import search from './icons/search-icon.png';
import menu from './icons/menu.png'
import smiley from './icons/smiley-face.png'

function Main(props){
    return(
        
        <div className = 'main-div' id = 'main'>

            <div className = 'nav'>
                <img src = {menu} className = 'menu' alt =''></img>
                <h1 className = 'app-name'>Recipe App</h1>
                <img src = {smiley} className = 'smiley' alt = ''></img>
            </div>
            
            <form className = 'form' onSubmit = {props.sendQuery}>
                <input type = 'text' placeholder = 'type to search' className = 'search-bar' value = {props.searchBarContent} onChange = {props.updateSearch}/>
                <img src = {search} className = 'search-icon' onClick = {props.sendQuery}/>
            </form>
        </div>
    )
}

export default Main;