import React, { useState, useEffect } from "react";
import "./CSS/app.css";
import Main from "./Main";
import Popular from "./Popular";

function App() {
  //NOTE THAT YOU USED YOUR EMAIL ADDRESS AS YOUR EMAIL ADDRESS, BASHORUN115 AS YOUR USERNAME, DOLAPO AS YOUR BUSINESS NAME, AND STAALMAN (ALL IN LOWERCASE) AS YOUR PASSWORD FOR THIS API

  const [recipes, setRecipes] = useState([]);
  const [query, setQuery] = useState("chicken");
  const [searchBarContent, setSearchBarContent] = useState("");
  const [searchFlag, setSearchFlag] = useState("false");
  const [calories, setCalories] = useState("");
  const [popularRecipeFlag, setPopularRecipeFlag] = useState(true);
  //FIRST STATE IS THE STATE VARIABLE TO STORE THE ARRAY THE APP RECIEVES FROM THE API
  //SECOND STATE IS THE STATE VARIABLE TO SET THE CONTENT OF THE QUERY TO BE PASSED INTO THE AJAX BLOCK BELOW
  //THIRD STATE IS THE STATE VARIABLE TO SAVE OR STORE WHATEVER THE USER TYPES INTO THE SEARCH BAR, BEFORE PADDING ITS CONTENTS INTO THE QUERY STATE ABOVE IT
  //FOURTH STATE IS THE STATE VARIABLE TO LOG CHANGES ONTO THE SEARCH ICON. IT BASICALLY FLIPS IT ON AND OFF, SO THE USEEFFECT HOOK CAN REALIZE THAT SOMETHING HAS CHANGED AFTER A USER LOGS SOMETHING INTO THE SEARCHBAR. THIS RELOADS THE API CALL, FETCHING A NEW ARRAY TO BE DISPLAYED
  //FIFTH STATE IS THE STATE VARIABLE THAT ATTEMPTS TO USE THE NUMBER OF CALORIES A DISH CONTAINS AS THE ID OF WHATEVER RECIPE IS CLICKED.

  const App_ID = "67ddc52d";
  const App_key = "9d9d7eba237042af213b5810e8b7ccd2	";
  const request = `https://api.edamam.com/search?q=${query}&app_id=${App_ID}&app_key=${App_key}`;

  const apiQuery = async () => {
    const response = await fetch(request);
    const data = await response.json();
    console.log(data.hits);
    setRecipes(data.hits);
  };

  useEffect(() => {
    apiQuery();
  }, [searchFlag]);

  //THE ABOVE IS THE ASYNC/AWAIT AJAX BLOCK TO MAKE A CALL TO THE API AND FETCH DATA

  const updateSearch = (e) => {
    e.preventDefault();
    setSearchBarContent(e.target.value);
  };

  const sendQuery = (e) => {
    e.preventDefault();
    setQuery(searchBarContent);
    setSearchFlag(!searchFlag);
    setSearchBarContent("");
  };

  

  return (
    <div className="app-main">
      {/*THIS IS THE CONTAINER FOR THE ENTIRE APP COMPONENT*/}
      <div className="container">
        <div className="body-section">
          <Main
            searchBarContent={searchBarContent}
            updateSearch={updateSearch}
            sendQuery={sendQuery}
          />
          {popularRecipeFlag && (
            <div className="popular-container">
              {recipes.map((recipes) => {
                return (
                  <Popular
                    image={recipes.recipe.image}
                    mealType={recipes.recipe.mealType}
                    cuisineType={recipes.recipe.cuisineType}
                    dishType={recipes.recipe.dishType}
                    label={recipes.recipe.label}
                    key={recipes.recipe.label}
                    insertNewComponent = {(e) => {
                      e.preventDefault();
                      const recipe = recipes.recipe
                      setCalories(recipe.calories);
                      //alert(recipe.calories)
                      //setPopularRecipeFlag(!popularRecipeFlag);
                    }}
                  />
                );
              })}
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default App;
