import React from "react";
import "./CSS/popular.css";
import testImage from "./images/testImage.jpg";

function Popular(props) {

  

  return (
        <div className="popular-recipe-card" onClick = {props.insertNewComponent}>
          <div className="recipe-image-div">
            <img src={props.image} className="recipe-image" alt="" />
          </div>
          <div className="recipe-description">
            <h3 className="meal-type">{props.cuisineType}</h3>
            <h1 className="recipe-name">{props.label}</h1>
            <div className="dish-cuisine-type">
              <h3 className="cuisine-type">{props.dishType}</h3>
              <h3 className="dish-type">{props.mealType}</h3>
            </div>
          </div>
        </div>
  );
}

export default Popular;
